.container {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    padding: 0 20px;
}

.containerHome {
    display: flex;
    flex-direction: column;
}

.containerText {
    width: 335px;
}

.containerImg {
    display: none;
}

@media only screen and (min-width: 768px) {
    .containerText {
        width: 704px;
    }

    .container {
        padding: 0 32px;
    }
}

@media only screen and (min-width: 1440px) {
    .container {
        flex-direction: row;
        justify-content: center;
        padding: 0 100px 100px;
    }

    .containerText {
        width: 575px;
    }

    .containerImg {
        display: flex;
        gap: 32px;
    }

    .img {
        width: 125px;
        height: 48px;
    }

    .titleUsers {
        font-size: var(--font-size-modal-title);
        font-weight: 400;
        letter-spacing: -0.02em;
        color: var(--main-text-color);
        margin-bottom: 12px;
        justify-content: center;
    }

    .tx {
        width: 210px;
        font-weight: 400;
        font-size: var(--font-size-smaller);
        letter-spacing: -0.02em;
        color: var(--regular-text-color);
    }
}

.text {
    font-weight: 400;
    font-size: var(--font-size-small);
    letter-spacing: 0.2em;
    text-transform: uppercase;
    color: var(--regular-text-color);
    margin-bottom: 10px;
}

.title {
    font-weight: 400;
    font-size: var(--font-size-title-mob);
    letter-spacing: -0.02em;
    margin-bottom: 14px;
    color: var(--main-text-color);
}

.span {
    text-decoration: underline;
    color: var(--button-color);
}

.textDesc {
    font-weight: 400;
    font-size: var(--font-size-small);
    letter-spacing: -0.02em;
    color: var(--regular-text-color);
    margin-bottom: 30px;
    width: 335px;
}

@media only screen and (min-width: 768px) {
    .text {
        font-size: var(--font-size-small);
        margin-bottom: 10px;
    }

    .title {
        font-size: var(--font-size-main-title);
        margin-bottom: 20px;
    }

    .textDesc {
        font-size: var(--font-size-smaller);
        margin-bottom: 40px;
        width: 533px;
    }
}

.containerButton {
    display: flex;
    gap: 20px;
    margin-bottom: 40px;
}

.buttonUp {
    background-color: var(--button-color);
    border-radius: 40px;

    padding: 14px 44px;
    width: 138px;
    height: 44px;
    font-weight: 600;
    font-size: var(--font-size-small);
    text-align: center;
    letter-spacing: -0.02em;
    color: var(--text-black-color);
    transition: background-color var(--transition-delay) var(--transition);

    &:hover {
        background-color: var(--hover-color);
    }
}

.buttonIn {
    background-color: transparent;
    border: 1px solid var(--regular-text-color);
    border-radius: 40px;

    padding: 14px 40px;
    width: 131px;
    height: 44px;

    font-weight: 400;
    font-size: var(--font-size-small);
    letter-spacing: -0.02em;
    text-align: center;
    color: var(--main-text-color);
    transition: border var(--transition) var(--transition-delay);

    &:hover {
        border: 1px solid var(--button-color);
    }
}

@media only screen and (min-width: 768px) {
    .containerButton {
        margin-bottom: 36px;
    }

    .buttonUp {
        width: 145px;
        height: 47px;

        font-size: var(--font-size-smaller);
    }

    .buttonIn {
        width: 137px;
        height: 47px;

        font-size: var(--font-size-smaller);
    }
}

@media only screen and (min-width: 1440px) {
    .containerButton {
        margin-bottom: 166px;
    }
}

@media (max-width: 374.99px) {
    .containerText {
        width: 270px;
    }

    .buttonIn,
    .buttonUp {
        padding: 14px 35px;
    }

    .textDesc {
        width: 270px;
    }
}